<template>
  <div id="app" style="background-image: url(./static/background.png);">
    <div id="app-background">
      <header>
          <div @click="gamePrizes()">
          <img src="../static/GamePrizesIcon.png" alt="游戏奖品">
      </div>
      <div @click="RulesGame()">
          <img src="../static/RulesGameIcon.png" alt="游戏规则">
      </div>
      </header>
      <GameIcons ref="myGameIcons"></GameIcons>
  </div>
    <Projects> </Projects> 
    <AppBottom @mychouQu="mychouQu()" @myclick="clickHave()"></AppBottom>
    <GamePrizes @on-typeShou-hide="chindHide()" :title="titleBage" :typeShou="GamePrizesType"></GamePrizes>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'
import Projects from "./components/projects";
import GameIcons from "./components/gameIcons.vue";
import GamePrizes from "./components/GamePrizes.vue";
import AppBottom from "./components/appBottom.vue";
export default {
  name: 'App',
  components: {
    HelloWorld,
    Projects,
    GameIcons,
    GamePrizes,
    AppBottom
  },
   data () {
    return {
      titleBage:"onBangne",
      GamePrizesType:false
    }
  },
  methods:{
    mychouQu:function(){
      this.$refs.myGameIcons.chouQu();
    },
    clickHave:function(){
      this.$refs.myGameIcons.chouJiang();
    },
    gamePrizes:function(){
      this.titleBage="GamePrizes"
      this.GamePrizesType = !this.GamePrizesType
    },
    RulesGame:function(){
      this.titleBage = "RulesGame"
      this.GamePrizesType = !this.GamePrizesType
    },
    chindHide:function(val){
      console.log(val)
      if("onBangne" == val){
 this.GamePrizesType = false
      }else{
        this.GamePrizesType = !this.GamePrizesType
      }
     
    }
  }
}
</script>

<style>
html,body{
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}
#app {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}
#app-background {
  margin-top: 60%;
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
}
header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
header img{
    width: 100px;
    height: 35px;
}
</style>



