<template>
  <div @click="Moduhide()" id="app-iconMu" :class="{hide:hide}">
  </div>
</template>

<script>
export default {
  name: 'projects',
  data () {
    return {
     hide:false
    }
  },
  methods: {
    Moduhide:function(){
      this.hide=!this.hide
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@keyframes myfirst
{
    from {transform: rotate(0deg);}
    to {transform: rotate(360deg);}
}
#app-iconMu{
  position: absolute;
  top: 7%;
  left: 2px;
  width: 30px;
  height: 30px;animation-delay: -2s;
  animation: myfirst 3s;
  animation-iteration-count: infinite;
  background-image: url('../../static/round.png');
  background-repeat: no-repeat;
  background-size: cover;
}
#app-iconMu.hide{
  background-image: url('../../static/roundNo.png');
}
</style>



