<script >
export default {
  name: 'GamePrizes',
  props:["typeShou","title"],
  data () {
    return {
      
    }
  },
  watch:{
    
  },
  created:function(){
    setTimeout(this.hide,1000 * 5,'onBangne')
  },
  methods:{
    hide:function(val){
       this.$parent.chindHide( val ?val : this.title);
    }
  }
}
</script>

<template>
 <div @click.self="hide()" v-show="typeShou" id="GamePrizes">
   <img v-show="title == 'GamePrizes'" src="../../static/GamePrizes.jpg" alt="">
   <img v-show="title == 'RulesGame'" src="../../static/RulesGame.jpg" alt="">
   <img v-show="title == 'onBangne'" src="../../static/onBangne.jpg" alt="">

   
 </div>
</template>

<style >
#GamePrizes{
  position:absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 80%;
  height: 90%;
   padding:10% 10% 0;
}
#GamePrizes img{
width: 100%;
height: 100%;
}
</style>



