<template>
  <transition name="fade" @after-leave="handleAfterLeave">
    <div
      v-show="visible"
      class="message"
      :class="[`${type}`]"
      @mouseleave="startTimer"
      @mouseenter="clearTimer"
    >
      {{ message }}
    </div>
  </transition>
</template>

<script>
export default {
  name: 'Message',
  data() {
    return {
      visible: false,
      type: 'info',
      message: '',
      duration: 3000,
      timer: null
    }
  },
  mounted() {
    this.setTimer() // 组件挂载的时候开始计时，计时完成移除
  },
  methods: {
    setTimer() { // 组件停留时间duration
      this.timer = setTimeout(() => {
        this.close()
      }, this.duration)
    },
    close() {
      this.visible = false
      clearTimeout(this.timer)
    },
    clearTimer() { //鼠标移入提醒框时，清除定时器，提醒框持久化显示
      console.log('清除定时器，不会自动消失')
      clearTimeout(this.timer)
    },
    startTimer() { // 鼠标离开提醒框，定时器开始计时
      console.log('开始定时器')
      this.setTimer()
    },
    handleAfterLeave() { // 移除动画执行完成后，销毁组件，并将其从父节点移除
      this.$destroy(true)
      this.$el.parentNode.removeChild(this.$el)
    }
  }
}
</script>

<style scoped >
.fade-enter-active {
  animation: fade-in 0.6s ease;
}
.fade-leave-active {
  animation: fade-out 0.6s ease;
}
@keyframes fade-in {
  0% {
    top: -40px;
    opacity: 0;
  }
  100% {
    top: 20px;
    opacity: 100%;
  }
}
@keyframes fade-out {
  0% {
    top: 20px;
    opacity: 100%;
  }
  100% {
    top: -40px;
    opacity: 0;
  }
}
.message {
  position: absolute;
  top: 20px;
  left: 50%;
  z-index: 999;
  min-width: 200px;
  height: 40px;
  transform: translateX(-50%);
  line-height: 40px;
  padding: 0 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.info {
  background-color: rgba(0, 0, 0, 0.2);
}
.success {
  background-color: rgba(0, 255, 255, 0.2);
}
.warning {
  background-color: rgba(0, 0, 0, 0);
}
.error {
  background-color: rgba(0, 0, 0, 0);
}
</style>


