<template>
    <ol class="footer">
      <li :style="{'acver':myClick}" @click="clickHave()">{{ myClick ? "今日已集" : "点击集卡" }}</li>
      <li>好友帮忙</li>
      <li @click="chouQu()">抽取奖品</li>
    </ol>
</template>
<script>
export default {
  name: "appBottom",
  props: [""],
  data() {
    return {
      myClick: false,
    };
  },
  methods: {
      clickHave:function(){
          this.$emit('myclick')
      },
      chouQu:function(){
          this.$emit('mychouQu')
      }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.acver{
    background: linear-gradient(rgb(133, 124, 124),rgb(56, 56, 53));
}
.footer {
  display: flex;
  padding: 0 15px;
  justify-content: space-between;
}
li {
  background-color: burlywood;
  padding: 10px 15px;
  color: aliceblue;
  border-radius: 10px;
  font-size: 15px;
  list-style: none;
}
</style>



