<script >
import AppBottom from "./appBottom.vue";
export default {
  name: "gameIcons",
  components: {
    AppBottom,
  },
  data() {
    return {
      massge: "",
      items: [
        {
          img: "../assets/icon1.png",
          count: 1,
          className: false,
        },
        {
          img: "../../static/icon2.png",
          count: 0,
          className: false,
        },
        {
          img: "../../static/icon3.png",
          count: 1,
          className: false,
        },
        {
          img: "../../static/icon4.png",
          count: 0,
          className: false,
        },
        {
          img: "../../static/icon5.png",
          count: 0,
          className: false,
        },
        {
          img: "../../static/icon6.png",
          count: 0,
          className: false,
        },
      ],

      titles: [
        {
          img: "",
          msg: "1111",
        },
        {
          img: "",
          msg: "5555555555555555",
        },
        {
          img: "",
          msg: "尝试试错法",
        },
        {
          img: "",
          msg: "222222222222222",
        },
        {
          img: "",
          msg: "6665555",
        },
      ],
    };
  },
  created: function () {
    let tieel = setInterval(this.Scorll, 1000 * 3);
  },
  watch: {},
  methods: {
    Scorll: function () {
      this.titles.unshift(this.titles.pop());
    },
    chouQu: function () {
      let everyArrays = this.items.every(function (item) {
        return item.count >= 1;
      });

      if (everyArrays) {
         this.$notice({
          type: "success",
          message: "你已集齐所有卡片",
        });
      } else {
        this.$notice({
          type: "info",
          message: "你还未集齐所有卡片",
        });
      }
    },
    chouJiang: function () {
      let count = Math.round(Math.random() * (this.items.length - 1));
      this.items[count].count = ++this.items[count].count;
      this.items[count].className = true;
      var till = setTimeout(() => {
        this.items[count].className = false;
        clearTimeout(till);
      }, 1000 * 1.5);
    },
  },
};
</script>

<template>
  <div id="app-connet">
    <ul>
      <li
        :class="{ anmerUp: item.className }"
        v-for="(item, index) in items"
        :key="index"
      >
        <img
          :class="{ imgfilter: !item.count }"
          :src="
            'https://zhoushuheng01.github.io/zhou.github.io/static/icon' +
            (index + 1) +
            '.png'
          "
          alt=""
        />
        <span class="">{{ item.count }}</span>
      </li>
    </ul>
    <transition-group tag="ol" name="">
      <li class="list-complete-item" v-for="item in titles" :key="item.msg">
        <img src="../../static/toux.jpg" alt="" />
        <p class="">{{ item.msg }}</p>
      </li>
    </transition-group>
  </div>
</template>

<style scoped>
@keyframes example {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(0px);
  }
}
.anmerUp {
  animation-name: example;
  animation-duration: 1.5s;
}
#app-connet ul {
  position: relative;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding-left: 0;
}
#app-connet ul li {
  display: inline-block;
  position: relative;
  margin: 4%;
  margin-top: 0;
  margin-right: 0;
}
#app-connet ul li img {
  display: block;
  width: 70px;
  height: 70px;
}
ol {
  padding: 0;
  height: 30px;
  overflow: hidden;
}
ol li {
  list-style: none;
  display: flex;
  height: 30px;
  align-items: center;
  overflow: hidden;
  justify-content: center;
}
ol li img {
  width: 15px;
  height: 15px;
  margin-top: 2px;
  margin-right: 5px;
  border-radius: 50%;
}
ol li p {
  margin: 0;
}
.imgfilter {
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);

  filter: grayscale(100%);

  filter: gray;
}
span {
  position: absolute;
  right: 0;
  top: 0;
  padding: 2px 5px;
  border-radius: 50%;
  font-size: 12px;
  color: azure;
  text-align: center;
  background-color: coral;
}

.list-complete-item {
  transition: all 1s;
}
.list-complete-enter, .list-complete-leave-to
/* .list-complete-leave-active for below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(40px);
}
</style>



